<template>
    <div :class="['popup', {'on':isOn}]">
        <div class="container">
            <div class="title">
                <h1>{{타이틀}}</h1>
            </div>
            <div class="content">
                <ul>
                    <li v-for='item in 모달내용' :key="item">{{item}}</li>
                </ul>
            </div>
            <div class="button-box">
                <button @click.prevent="onClickPopupClose" class="close-btn" title="닫기">닫기</button>
            </div>
        </div>
    </div>
</template>

<script>
import eventBus from '../../eventBus'

export default {
    name:'ModalComponent',
    props:[],
    data(){
        return{
            타이틀:'9월 JUST 쇼핑몰 세일 안내',
            모달내용:[
                '오랜만에 찾아온 세일 안내!',
                'JUST 쇼핑몰이 제안하는 유니크한 시즌별 패션 트렌드를 즐겨보세요.',
                '오랜만에 찾아온 휴면고객이라면, 지금 바로 로그인 하고 적립금 1,000원을 받으세요!'
            ],
            isOn: false
        }
        
    },
    created(){
        eventBus.$on('popupOpen', (번호)=>{
            if(번호===0){
                // 모달창띄우기
                this.isOn=true;
            }
        });
    },
    methods:{
        onClickPopupClose(){
            this.isOn = false;
        }
    }
}
</script>

<style scoped>
#wrap .popup {
z-index: 4;
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: rgba(0,0,0, 0.5);

/* 팝업창 가운데 */
display: flex;
justify-content: center;
align-items: center;
display: none;
}

#wrap .popup.on {
display: flex;
}

#wrap .popup .container {
width: 500px;
background: #fff;
border: 1px solid #ccc;
padding: 20px;
}

#wrap .popup .container .title {width:100%;}
#wrap .popup .container .title h1 {
font-size: 20px;
font-weight: 600;
color:#222;
border-bottom: 1px solid #ddd;
padding-bottom: 20px;
}

#wrap .popup .container .content {
padding-top: 20px;
}
#wrap .popup .container .content ul {width:100%;}
#wrap .popup .container .content ul li {width:100%;line-height: 180%;}

#wrap .popup .container .button-box {padding-top: 50px;text-align: right;}
#wrap .popup .container .button-box button {
width: 60px;
height: 35px;
border: 1px solid #ccc;
background: #fff;
color: #333;
cursor: pointer;   
border-radius: 3px; 
font-weight: 500;
}
#wrap .popup .container .button-box button:hover {
background: #555;
color: #fff;
}
</style>